import { createMuiTheme } from '@material-ui/core/styles';

export const darkTheme = createMuiTheme({
    palette: {
        type: 'dark',
        primary: {
            main: '#03f74a'
        },
        white: '#fffff',
        secondary: {
            main: '#0fc7d5'
        },
        grey: {
            main: '#efefef'
        }
    }
});

export const lightTheme = createMuiTheme({
    palette: {
        type: 'light',
        primary: {
            main: '#03f74a'
        },
        white: '#fffff',
        secondary: {
            main: '#0fc7d5'
        },
        grey: {
            main: '#efefef'
        }
    }
});